
.tabs-menu {
  color: whitesmoke;
  position: fixed;
  top: 1em;
  left: 1em;
  z-index: 99;
}
.tabs-menu ul {
    border: none;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-negative: 1;
        flex-shrink: 1;
}
.tabs-menu .active a {
    border-bottom: 2px solid #dbdbdb;
    color: #dbdbdb;
}
@media (min-width: 500px) {
.tabs-menu {
      left: unset;
      right: 2em;
}
}
.tabs a {
  border-bottom: 2px solid transparent;
  color: whitesmoke;
  padding: 0.5em;
  transition: all 86ms ease-out;
}
.tabs a:hover {
    border-bottom: 2px solid #dbdbdb;
    color: #dbdbdb;
}
.header {
  background: rgba(17, 34, 51, 0.9);
  position: fixed;
  height: 4.5em;
  width: 100vw;
  top: 0;
  left: 0;
  transition: -webkit-transform 86ms ease-out;
  transition: transform 86ms ease-out;
  transition: transform 86ms ease-out, -webkit-transform 86ms ease-out;
  z-index: 9;
  -webkit-transform: translateY(-4.5em);
          transform: translateY(-4.5em);
}
.sticky-header .header {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
.logo {
  display: none;
  fill: rgba(255, 255, 255, 0.8);
  height: 50px;
  width: auto;
  transition: -webkit-transform 86ms ease-out;
  transition: transform 86ms ease-out;
  transition: transform 86ms ease-out, -webkit-transform 86ms ease-out;
  -webkit-transform: translate(2em, 0.3em);
          transform: translate(2em, 0.3em);
}
@media (min-width: 500px) {
.logo {
      display: inline-block;
}
}
.hero {
  color: #fff;
  text-align: center;
  height: 80vh;
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  width: 100vw;
}
.hero.is-primary {
    background-blend-mode: hard-light;
    background-color: rgba(0, 0, 0, 0.25);
}
.hero-body {
  padding-top: 26vh;
}
.title {
  font-weight: 500;
}
.hero-body .title,
.hero-body .subtitle {
  opacity: 1;
  position: relative;
  transition: all 0.3s;
  z-index: 1;
}
.sticky-header .hero-body .title, .sticky-header
  .hero-body .subtitle {
    opacity: 0;
    -webkit-transform: translateY(-1em);
            transform: translateY(-1em);
}



